*, :after, :before {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #fff;
  background-color: #0c0a00;
  font-family: Bebas Neue, sans-serif;
  font-weight: 400;
}

.hero {
  height: 100vh;
}

.hero__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__text-content {
  position: absolute;
  bottom: 10%;
  left: 15%;
}

.hero__title {
  letter-spacing: 1px;
  font-size: 45px;
}

.hero__text {
  width: 400px;
  font-size: 20px;
}

.content {
  padding: 100px 0;
}

.content__item {
  justify-content: center;
  gap: 40px;
  margin-bottom: 100px;
  display: flex;
}

.content__item:nth-child(2n) {
  flex-direction: row-reverse;
}

.content__info {
  width: 400px;
}

.content__title {
  margin-bottom: 15px;
  font-size: 25px;
}

.content__text {
  margin-bottom: 15px;
}

.content__link {
  text-decoration: underline;
}

.feedback-form {
  max-width: 480px;
  flex-direction: column;
  margin: 0 auto;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}



